<template>

  <el-row :gutter="10">
    <div v-for="(data, index) in showNumber" :key="index">
      <el-col :span="item_span">
        <el-card :body-style="{ padding: '5px' }">
          <img :src="objs[index].CDNUrl" class="image">
          <div class="content">

            <time class="time">{{ currentDate }}</time>

            <span>{{ objs[index].copyright}}</span>
          </div>
        </el-card>
      </el-col>
    </div>
  </el-row>
</template>

<script>
  export default {
    name: "DataCard",
    props: {
      showNumber: Number,
      objs: [],
      ot: Number
    },
    data() {
      return {
        currentDate: new Date(),
        item_span: 24 / this.showNumber,
      }
    },
    mounted() {
      if (this.showNumber > this.objs.length) {
        this.showNumber = this.objs.length;
        this.item_span = 24 / this.showNumber
      }
    }
  }
</script>

<style scoped>


  .image {

    width: 40%;
    height: 40%;

  }

  .content {
    padding: 14px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
</style>



