<template>
  <div>

    <div v-for="(val, key, index) in data_maps">
      <DataCard :show-number="3" :objs="val" :ot="index"/>
    </div>
  </div>
</template>

<script>


  import DataCard from "./DataCard";


  export default {
    name: "Tab3",
    components: {DataCard},

    data() {
      return {
        data_maps: {},
      }
    },

    mounted() {
      this.getShowDatas()
    },
    methods: {
      getShowDatas() {

        this.axios.get('https://bing.getlove.cn/latelyBingImageStory')
          .then(data => {

            let index = 0;
            let map_index = 0;
            let temp_obj;
            let temp_maps = {}
            for (let item of data.data) {
              if (index % 3 === 0) {
                temp_obj = [];
                temp_maps[map_index + ''] = temp_obj;
                map_index++;
              }
              temp_obj.push(item);
              index++;
            }

            this.data_maps = Object.assign({}, this.data_maps, temp_maps)


          }).catch(error => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>

</style>



